// import {Link} from "gatsby"
import React, { useState, useEffect } from 'react';
import { Button, Link } from "react-scroll";
import styled from 'styled-components'
import Drawer from 'rc-drawer';

const Navigation = styled.nav``
const Toggle = styled.div``
const Navbox = styled.div``
const Hamburger = styled.div``

const Navbar = () => {


  const [navbarOpen, setNavbarOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  // change state on scroll
  useEffect(() => {
    const handleScroll = () => {
      const isScrolled = window.scrollY > 95;
      if (isScrolled !== scrolled) {
        setScrolled(!scrolled);
      }

    };

    document.addEventListener('scroll', handleScroll, { passive: true });

    return () => {
      // clean up the event handler when the component unmounts
      document.removeEventListener('scroll', handleScroll);
    };
  }, [scrolled]);

  return (

    <header className={scrolled ? "fixed h-24 sm:h-32 flex items-center z-30 shadow-sm border-b-2 border-pink-500 w-full bg-white dark:bg-gray-800" : "h-24 sm:h-32 flex items-center z-30 w-full dark:bg-gray-800"}>

      <div className="container flex items-center justify-between px-6 mx-auto">
        <div className="text-3xl font-black text-gray-800 uppercase dark:text-white">
          <button href="#" className="font-extrabold uppercase focus:outline-none">
            <Link activeClass="active" to="home" spy={true} smooth={true} offset={-200} duration={500}>
              <span className="text-pink-500">Soham</span>.Manoli</Link></button>
        </div>
        <div className="flex items-center">
          <nav className="items-center hidden text-lg text-gray-800 uppercase font-sen dark:text-white lg:flex">


            <button href="#" className="flex px-6 py-2 focus:outline-none">
              <Link activeClass="active" to="about" spy={true} smooth={true} offset={-200} duration={500}>
                About Me &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img">🙎🏽‍♂️</span>
              </Link></button>
            {/* <a href="#about" className="flex px-6 py-2">
                   Awards &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img">🥇</span>
                </a> */}
            <button href="#" className="flex px-6 py-2 focus:outline-none">
              <Link activeClass="active" to="projects" spy={true} smooth={true} offset={-200} duration={500}>
                Projects &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img">⚡</span>
              </Link>
            </button>
            <button href="#" className="flex px-6 py-2 focus:outline-none">
              <Link activeClass="active" to="skills" spy={true} smooth={true} offset={-120} duration={500}>
                Skills &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img">⚔</span>
              </Link>
            </button>
            <button href="#" className="flex px-6 py-2 focus:outline-none">
              <Link activeClass="active" to="contact" spy={true} smooth={true} offset={-70} duration={500}>
                Contact Me &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img">💬</span>
              </Link>
            </button>
          </nav>
          <button className="flex flex-col ml-4 lg:hidden" onClick={() => setNavbarOpen(!navbarOpen)}>
            <span className="w-6 h-1 mb-1 bg-gray-800 dark:bg-white">
            </span>
            <span className="w-6 h-1 mb-1 bg-gray-800 dark:bg-white">
            </span>
            <span className="w-6 h-1 mb-1 bg-gray-800 dark:bg-white">
            </span>
          </button>

          {/* <Drawer className="w-full h-0" open={navbarOpen} onClose={!navbarOpen} handler={false} placement="left">
      
            <div className="w-full bg-gray-800"> 
              <Button onClick={() => setNavbarOpen(false)}>Click Me</Button>
            </div>
          </Drawer> */}
        </div>
      </div>
    </header>

  )
}

export default Navbar